import React from 'react';
import './scheduled.css';

class Schedule extends React.Component {
    state = {
        pastUrls: new Array,
        futureUrls: new Array,
        pastShowing: false,
        loading: true
    }
    componentDidMount() {
        let useCook = document.cookie.match(new RegExp('(^| )' + 'token' + '=([^;]+)'));
        console.log('cookie', useCook)
        if (useCook) {
        let useCooki = useCook[0].split('=')
        let useCookie = useCooki[1]
        let userId = localStorage.getItem('userId')
        let obj = {"userId": userId}
        setTimeout(() => {
        fetch('https://multer-test123.herokuapp.com/getScheduled', {
            headers: {
                'Authorization': `Bearer ${useCookie}`,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(obj),
            method: 'POST'
        })
        .then(res => res.json())
        .then(async res => {
            console.log('res', res)
            let data = res.Success;
            let arrayPast = new Array;
            let arrayFuture = new Array;
            await data.map(x => {
                let name = x.data; let time = x.time;
                let currentTime = new Date().getTime()
                console.log(currentTime, time, currentTime > time)

                let object = {name, time}
                return fetch(x.location)
                .then(res => res.blob())
                .then(data => {
                    console.log('d',data)
                    let reader = new FileReader();
                    reader.readAsDataURL(data)
                    //console.log(reader)
                    reader.onloadend = function() {
                        var base64data = reader.result;      
                        let newObject = {...object, "img": base64data}     
                        if (currentTime > time) {     
                            arrayPast.push(newObject)
                        } else {
                            arrayFuture.push(newObject)
                        }
                    }
                })
            })
            setTimeout(() => {
                this.setState({futureUrls: arrayFuture})
                this.setState({pastUrls: arrayPast})
                this.setState({loading: false})
                this.forceUpdate()
            }, 1000)
        })
        }, 500)
        }
    }
    timeConverter(UNIX_timestamp){
        var a = new Date(Number(UNIX_timestamp));
        console.log(new Date(Number(UNIX_timestamp)))
        var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        console.log(date, month)
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var longDate = date + ' ' + month + ' ' + year;
        let shortDate = hour + ':' + min;
        return (
            <div className="timings">
            <span>Date: {longDate} </span> 
            <span>Time: {shortDate} </span>
            </div>
        )
      }
      toggle = () => {
          this.setState((prev) => ({
            pastShowing: !prev.pastShowing
          }))
      }
    render() {
        const {pastUrls, futureUrls, pastShowing, loading} = this.state;
        return (
            <div className="scheduled">
                <h1>Scheduled posts</h1>
                {!loading &&
                <div className="btns">
                    <button 
                    onClick={this.toggle}
                    >{pastShowing ? "Past Posts" : "Scheduled Posts"}</button>
                </div>
                }
                {loading &&
                <div className="lds-defaultTwo left">
                <div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
                </div>
                }
                
                <div className="flex">
                {pastShowing === true &&
                pastUrls.length > 0 && (
                    pastUrls.map((x, i) => {
                      return (
                          <div key={i} className="item">
                              <img style={{width: 180, height: 180}} src={x.img} />
                              {this.timeConverter(x.time)}
                          </div>
                      )
                    })
                )}

                {pastShowing === false &&
                futureUrls.length > 0 && (
                    futureUrls.map((x, i) => {
                      return (
                          <div key={i} className="item">
                              <img style={{width: 180, height: 180}} src={x.img} />
                              {this.timeConverter(x.time)}
                          </div>
                      )
                    })
                )}
                </div>
            </div>
        )
    }
}

export default Schedule;